import "./Review.scss";
import flower4 from "../../assets/images/flower4.png"

const Review = () => {
    return (
        <section className="review">
            <div className="container">
                <div className="review__content">
                    <h2 className="review__title">
                      
                    </h2>
                    <button className="review-btn"> </button>
                </div>
                <div className="review__wrapper">
                    <button className="review__bt review-prev"></button>
                    <div className="div review__inner">
                        <ul className="stars__list">
                            <li className="stars__item">
                                <button className="stars__btn">
                                <i className="fa-solid fa-star"></i>
                                </button>
                            </li>
                            <li className="stars__item">
                                <button className="stars__btn">
                                <i className="fa-solid fa-star"></i>
                                </button>
                            </li>
                            <li className="stars__item">
                                <button className="stars__btn">
                                <i className="fa-solid fa-star"></i>
                                </button>
                            </li>
                            <li className="stars__item">
                                <button className="stars__btn">
                                <i className="fa-solid fa-star"></i>
                                </button>
                            </li>
                            <li className="stars__item">
                                <button className="stars__btn">
                                <i className="fa-solid fa-star"></i>
                                </button>
                            </li>
                        </ul>
                        <h3 className="review__subtitle">  </h3>
                        <p className="review__desc">    ,     !   3       !  ,  ,  !        -  !  24 -   ,       .</p>
                        <div className="price__wrapper">
                            <p className="review__price">
                              :  
                            </p>
                            <span className="review__price-inner"> 80 </span>
                        </div>
                    </div>
                    {/* eslint-disable-next-line */}
                    <img src={flower4} className="review__img" alt="review image" width={200} height={200} />
                    <button className="review-next"></button>
                </div>
                <button type="button" className="review__button"> </button>
            </div>
        </section>
    )
}

export default Review;